.subtotal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 300px;
  height: 100px;
  padding: 20px;
  background-color: lightgray;
  border: lightgray 1px solid;
  border-radius: 4px;
}

.subtotal > button {
  background: #f0c14b;
  border-radius: 2px;
  width: 50%;
  padding: 5px;
  border: 1px solid;
  margin-top: 8px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}
