.header {
  height: 60;
  display: flex;
  align-items: center;
  background-color: rgb(66, 121, 176);
  position: sticky;
  top: 0;
  z-index: 100;
}
.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
}
.header__search {
  display: flex;
  flex: 1;
}
.header__searchInputField {
  padding: 10px;
  height: 8px;
  width: 100%;
}
.header__searchIcon {
  padding: 5px;
  height: 22px;
}
.header__optionLineOne {
  font-size: 10px;
}
.header__optionLineTwo {
  font-size: 15px;
  font-weight: bold;
}
.header__navbar {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
}
.header__optionBasket {
  display: flex;
  padding: 5px;
  height: 22px;
  align-items: center;
  margin-left: 10px;
  margin-right: 10px;
}
